export default {
	data() {
		return {};
	},
	methods: {
		//跳转页面
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		myajax: function() {
			//调取相册
			uni.chooseImage({
				count: 1, //默认9
				success: function(res) {
					console.log(JSON.stringify(res))
					uni.request({
					    url: getApp().globalData.byurl + '/buyer/base/upload', 
						method:'POST',
					    data: {
					        scene: 'idcard',
							file:res.tempFilePaths[0]
					    },
					    success: (res) => {
					        console.log(res.data)
					    }
					});
				},
				fail: function(fa) {
					//提示框
					uni.showToast({
						title: '失败，请重试',
						duration: 2000
					});
				}
			});
		}
	}
};
